html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
  display: flex;
}

header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  gap: 20px;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.li {
  margin-right: 20px;
  display: inline-block;
}

.li:last-child {
  margin: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

:root {
  --link-color-hover: #00acdc;
}

.nav_link:hover, .nav_link.is-active {
  color: var(--link-color-hover);
}

.nav_link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
